import React, { Component } from 'react';
import { connect } from 'react-redux';

// Actions
import {
    addTask
} from '../../actions';

//Config
import { MAX_LENGTH } from '../../config';

class AddTask extends Component {

    constructor (props) {
        super(props);
        this.state = {
            newTaskMessage: '',
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange (event) {
        this.setState({
            newTaskMessage: event.target.value,
        });
    }

    handleSubmit (event) {
        event.preventDefault();

        const { dispatch } = this.props;

        if (this.state.newTaskMessage.trim() === '') {
            return;
        }

        const newTask = {
            message:   this.state.newTaskMessage,
            completed: false,
            favorite:  false,
            created:   Date.now(),
        };

        dispatch(addTask({ newTask }));

        this.setState({
            newTaskMessage: '',
        });
    }

    render () {
        return (
            <form
                onSubmit = { this.handleSubmit }>
                <input
                    maxLength = { MAX_LENGTH }
                    placeholder = 'ÐÐ¿Ð¸ÑÐ°Ð½Ð¸Ðµ Ð¼Ð¾ÐµÐ¹ Ð½Ð¾Ð²Ð¾Ð¹ Ð·Ð°Ð´Ð°ÑÐ¸'
                    type = 'text'
                    value = { this.state.newTaskMessage }
                    onChange = { this.handleChange }
                />
                <button>ÐÐ¾Ð±Ð°Ð²Ð¸ÑÑ Ð·Ð°Ð´Ð°ÑÑ</button>
            </form>
        );
    }
}

export default connect()(AddTask);
